.main {
  display: flex;
  width: 1000px;
  min-width: 800px;
  height: 80%;
  min-height: 600px;
}
.main .user_list {
  background: #2e3238;
  position: relative;
  width: 280px;
  color: #fff;
}
.main .user_list .ui_top {
  display: flex;
  align-items: center;
  padding: 18px;
}
.main .user_list .ui_top img {
  width: 40px;
  height: 40px;
  margin-right: 10px;
}
.main .user_list .ui_top span {
  font-size: 18px;
  width: 156px;
}
.main .user_list .ui_top i {
  color: #6e6e6f;
  font-size: 20px;
  margin-left: 8px;
  cursor: pointer;
  position: relative;
}
.main .user_list .ui_top i ul {
  position: absolute;
  right: 0;
  top: 30px;
  background: #fff;
  -webkit-border-radius: 10px;
  font-size: 14px;
  z-index: 1;
}
.main .user_list .ui_top i ul li {
  height: 50px;
  width: 150px;
  text-align: center;
  line-height: 50px;
  border-bottom: 1px solid #f1f1f1;
  -webkit-border-radius: 10px;
}
.main .user_list .ui_top i ul li:hover {
  background: #f8f8f8;
}
.main .user_list .ui_search {
  display: flex;
  justify-content: center;
  margin-bottom: 6px;
}
.main .user_list .ui_search i {
  width: 32px;
  height: 32px;
  background: #26292e;
  color: #939497;
  font-size: 18px;
  text-align: center;
  line-height: 32px;
}
.main .user_list .ui_search input {
  width: 204px;
  height: 32px;
  padding-right: 10px;
  background: #26292e;
  color: #fff;
  font-size: 12px;
}
.main .user_list .ui_bottom .nav {
  display: flex;
  height: 35px;
  border-bottom: 1px solid #24272c;
}
.main .user_list .ui_bottom .list {
  position: absolute;
  top: 153px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  overflow-y: auto;
}
.main .user_list .ui_bottom .list::-webkit-scrollbar {
  width: 8px;
}
.main .user_list .ui_bottom .list::-webkit-scrollbar-thumb {
  background: #404449;
  border-radius: 4px;
}
