.cb_content {
  position: absolute;
  top: 51px;
  left: 0;
  right: 0;
  bottom: 180px;
  padding: 10px 19px 0;
  overflow-x: hidden;
  overflow-y: auto;
  text-align: left;
}
.cb_content::-webkit-scrollbar {
  width: 8px;
}
.cb_content::-webkit-scrollbar-thumb {
  background: #c3c3c3;
  border-radius: 4px;
}
.cb_bottom {
  display: flex;
  flex-direction: column;
  height: 179px;
  margin-right: 19px;
  border-top: 1px solid #d6d6d6;
  position: absolute;
  right: 0;
  bottom: 0;
  left: 0;
}
.cb_bottom span {
  display: flex;
  height: 30px;
  padding: 5px 17px;
}
.cb_bottom span i {
  font-size: 28px;
  margin-right: 10px;
  cursor: pointer;
  position: relative;
}
.cb_bottom span i ul {
  position: absolute;
  left: 0px;
  bottom: 30px;
  width: 248px;
  height: 180px;
  background: #fff;
  padding: 2px 5px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  z-index: 2;
}
.cb_bottom span i ul li {
  background: red;
  width: 32px;
  height: 32px;
}
.cb_bottom .input {
  box-sizing: content-box;
  height: 6em;
  overflow-x: hidden;
  overflow-y: auto;
  outline: none;
  margin-left: 20px;
}
.cb_bottom .input::-webkit-scrollbar {
  width: 8px;
}
.cb_bottom .input::-webkit-scrollbar-thumb {
  background: #c3c3c3;
  border-radius: 4px;
}
.cb_bottom .submit {
  display: flex;
  flex-direction: row-reverse;
  height: 28px;
  padding-top: 5px;
}
.cb_bottom .submit a {
  padding: 3px 30px;
  color: #222;
  border: 1px solid #c1c1c1;
  background: #fff;
  text-decoration: none;
}
.cb_bottom .submit a:hover {
  background: #f8f8f8;
}
